@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@800&display=swap);
:root {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f0f4f7;
  line-height: 1.6;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover{
  text-decoration: underline;
}

h1,
h2,
h3,
h4 {
  font-family: "Raleway";
  letter-spacing: -0.5px;
  line-height: 1.2;
}

h1 {
  font-size: 25px;
}

h3 {
  font-size: 25px;
}

h4 {
  font-size: 25px;
}

@media (min-width: 350px) {
  h1 {
    font-size: 30px;
  }
}

@media (min-width: 750px) {
  h1 {
    font-size: 40px;
  }
  
  h3 {
    font-size: 40px;
  }

  h4 {
    font-size: 32px;
  }
}

@media (min-width: 1000px) {
  h1 {
    font-size: 50px;
  }
}

img {
  max-width: 100%;
}

.mbxs {
  margin-bottom: 5px;
}
.mbs {
  margin-bottom: 10px;
}
.mbsm {
  margin-bottom: 18px;
}
.mbm {
  margin-bottom: 25px;
}
.mbl {
  margin-bottom: 45px;
}
.mbxl {
  margin-bottom: 60px;
}

@media (min-width: 1700px) {
  .outer{
    display: flex;
    margin-top: 90px;
    margin-bottom: 90px;
  }

  .outerContainer {
    width: 100%;
    max-width: 1500px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 40px rgba(0,0,0,0.2);
    margin: 0 auto;
    overflow: hidden;
  }
}

.container {
  position: relative;
  width: 100%;
  max-width: 1050px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (min-width: 400px) {
  .container {
    width: 95%;
    padding: 0;
  }
}

.center {
  text-align: center;
}

.spacer {
  margin-top: 90px;
  margin-bottom: 90px;
}

@media (min-width: 600px) {
  .spacer {
    margin-top: 120px;
    margin-bottom: 120px;
  }
}
.hero {
  background-color: #1e1c28;
  color: #fff;
  padding-top: 50px;
}

.heroInner {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.heroText {
  margin-top: auto;
  margin-bottom: auto;
  width: 100%;
  text-align: center;
}

.heroImage {
  margin-top: 30px;
  width: 100%;
  max-width: 400px;
}

.heroImage img {
  margin-bottom: -7px;
}

.techContainer {
  display: flex;
  flex-wrap: wrap;
}

@media (min-width: 600px) {
  .heroInner {
    flex-wrap: nowrap;
    justify-content: space-between;
  }

  .heroText {
    width: 60%;
    text-align: left;
  }

  .heroImage {
    width: 40%;
  }
}

.portfolioItem {
  padding: 60px 0;
  margin-bottom: 60px;
}

.portfolioItemInner {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.portfolioImage {
  width: 100%;
  display: flex;
}

.portfolioImage img{
  max-height: 300px;
  margin: 0 auto 30px;
  align-self: center;
}

.portfolioText {
  width: 100%;
  background-color: #fff;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.portfolioText h4 {
  margin-bottom: 30px;
}

.featureHeadline {
  font-weight: 700;
  color: #626262;
  margin-bottom: 8px;
  font-size: 18px;
}

.features {
  font-size: 16px;
  list-style: none;
}

.features li {
  position: relative;
  padding-left: 26px;
  margin-bottom: 6px;
}

.features li:before {
  content: "";
  width: 12px;
  height: 12px;
  position: absolute;
  color: grey;
  background: url(/static/media/checkmark.72e600cd.svg) center;
  background-size: cover;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

@media (min-width: 700px) {
  .portfolioItem {
    padding: 120px 0;
    margin-bottom: 90px;
  }
  
  .portfolioItemInner {
    flex-wrap: nowrap;
  }

  .portfolioImage {
    width: 50%;
    display: block;
  }

  .portfolioImage img{
    max-height: inherit;
    margin: 0;
  }
  
  .portfolioText {
    width: 50%;
    padding: 40px;
    margin-left: 50px;
  }

  .featureHeadline {
    font-size: 20px;
    margin-bottom: 12px;
  }
}
.tech {
  display: flex;
  align-items: center;
  width: 50%;
  margin-bottom: 25px;
  font-size: 0.95em;
  font-weight: 700;
}

.iconContainer {
  padding: 6px;
  padding-bottom: 1px;
  border-radius: 4px;
}

.tech span {
  display: inline-block;
  margin-left: 10px;
}

@media (min-width: 600px) {
  .tech {
    width: 33.33%;
    margin-bottom: 30px;
    font-size: 1.1em;
  }

  .iconContainer {
    padding: 10px;
    padding-bottom: 6px;
  }
  
  .tech span {
    margin-left: 15px;
  }
}
.contactDescription p {
  margin-bottom: 5px;
}

.contactContainer {
  padding: 90px 0;
  background-color: #1e1c28;
  color: #fff;
}

.contactDetails {
  text-align: center;
  font-size: 20px;
}

.contactDetails a {
  color: #fff;
}

@media (min-width: 600px) {
  .contactContainer {
    padding: 120px 0;
  }

  .contactDetails {
    font-size: 25px;
  }
}

